import React from 'react';
import { CSSTransition } from 'react-transition-group';

export const HorizSlide = ({shouldShow, timeout, classNames, children}) => {
  return (
    <CSSTransition
      timeout={timeout}
      classNames={classNames}
      in={shouldShow}>
      {children}
    </CSSTransition>
  );
}


